import { IVFOptions, EngineAPI } from '@player/IVFEngine';
import IEvent from '@player/event/IEvent';
import { CDN, ScaleMode } from '@player/core/model/IVFData';
import { Loading } from './Loading';
import { getDefaultCDN } from './getDefaultCDN';
import { getQueryVariable } from './getQueryVariable';
import { CreateApp } from './CreateApp';

declare let VFBUILDDATE: any; // 编译时间

declare let LAUNCHERVERION: any;

declare let VFVERSION: any;

declare let GUIVERSION: any;

declare let PLAYERRVERION: any;

(window as any)['vf'] && (window as any)['vf']['launcherPlayer'];

export function deleteVF() {
    const list = document.getElementsByName('vf-script');
    const vfLibs: any = [];

    list.forEach((value) => {
        if (value.title !== '' && value.hasAttribute('isLoad')) {
            vfLibs.push(value);
        }
    });
    while (vfLibs.length) {
        vfLibs[0].onload = null;
        vfLibs[0].onerror = null;
        vfLibs[0].remove();
        vfLibs.splice(0, 1);
    }
    const w = window as any;

    try {
        if (w) {
            if (w.vf) {
                if (w.vf.launcherPlayer && w.vf.launcherPlayer.dispose) {
                    w.vf.launcherPlayer.dispose();
                    w.vf.launcherPlayer = null;
                }
                delete w.vf;
            }
            if (w.gui) {
                delete w.gui;
            }
            if (w.VFConversion) {
                delete w.VFConversion;
            }
        }
    }
    catch (e) {
        //
    }
}
/**
 * vf集成方案加载器
 *
 */
export class VIPKIDLauncher {
    private readonly _ops: IVFOptions;

    /** 引擎与库的CDN */
    private _engineCDN: CDN;

    private _cdnsIndex = 0;
    private _loading: Loading;

    private _errorCount = 0;
    private _errorMax = 10;

    private completeCall?: Function;
    private errorCall?: Function;

    private version = LAUNCHERVERION;
    // eslint-disable-next-line no-undef
    private buildInfo = VFBUILDDATE;

    /**
     * 是否player项目
     */
    private _isPlayer = true;
    private _progress: HTMLScriptElement[] = [];
    /**
     * 对外接口 IVFEngineAPI
     */
    constructor(options: IVFOptions, completeCall: Function, errorCall?: Function) {
        if (completeCall === undefined) {
            throw new Error('VFLauncher constructor Expected 2-3 arguments');
        }
        this._loading = new Loading(options.container);

        if ((window as any)['vf'] === undefined) {
            (window as any)['vf'] = {};
        }

        if (!options.scaleMode) {
            options.scaleMode = ScaleMode.SHOW_ALL;
        }

        if (options.vfvars === undefined) {
            options.vfvars = {};
        }

        if (options.exclude === undefined) {
            options.exclude = [];
        }

        this._engineCDN = getDefaultCDN();

        // 配置的loading
        if (options.loading) {
            if (options.loading.position) {
                this._loading.position = options.loading.position;
            }
            if (options.loading.image) {
                this._loading.image = options.loading.image;
            }
        }
        options.debugVFPath = getQueryVariable('proxyVF') || options.debugVFPath;
        options.debugGuiPath = getQueryVariable('proxyGUI') || options.debugGuiPath;
        options.debugPlayerPath = getQueryVariable('proxyPlayer') || options.debugPlayerPath;
        this._ops = options;
        this.completeCall = completeCall;
        this.errorCall = errorCall;
        this.loadJs();
    }

    /**
     * 环境依赖配置，可以读取一个engine-vserion.json文件获取版本依赖，由于还需要单独加载（本地动态脚本替换不科学～），为了速度，暂缓修改。
     */
    private getLibs(index: number) {
        // if (process.env.NODE_ENV === 'production')
        const ops = this._ops;
        const cdn = this._engineCDN.default[index];
        const libs: { url: string; version: string }[] = [];
        const canLibs: { url: string; version: string }[] = [];

        if (ops.debug) {
            libs.push({ url: `${cdn}vf/engine/debug/vconsole.min.js`, version: 'vconsole' });
        }

        if (ops.showFPS) {
            libs.push({ url: `${cdn}vf/engine/debug/stats.min.js`, version: 'stats' });
        }

        let libUrl = '';

        if (ops.exclude) {
            if (ops.exclude.indexOf('vf') === -1) {
                libUrl = ops.debugVFPath || `${cdn}vf/engine/vf-v${VFVERSION}/vf.min.js`;
                libs.push({ url: libUrl, version: `vf-v${VFVERSION}` });
            }

            if (ops.exclude.indexOf('gui') === -1) {
                libUrl = ops.debugGuiPath || `${cdn}vf/engine/gui-v${GUIVERSION}/gui.min.js`;
                libs.push({ url: libUrl, version: `gui-v${GUIVERSION}` });
            }

            if (ops.exclude.indexOf('player') === -1) {
                libUrl = ops.debugPlayerPath || `${cdn}vf/engine/player-v${PLAYERRVERION}/player.min.js`;
                libs.push({ url: libUrl, version: `player-v${PLAYERRVERION}` });
            }
            else {
                this._isPlayer = false; // 独立项目，非player项目
            }
        }

        if (ops.libs) {
            ops.libs.forEach((value, index) => {
                libs.push({ url: value, version: `vf-lib-${index}-${performance.now()}` });
            });
        }

        libs.forEach((value) => {
            if (document.getElementById(value.version) === null) {
                canLibs.push(value);
            }
        });

        return canLibs;
    }

    private loadJs() {
        const libs = this.getLibs(this._cdnsIndex);

        if (libs.length === 0) {
            if (!this.checkLoadStates()) {
                return;
            }
            this.createEngine();

            return;
        }

        this._loading.show();
        libs.forEach((item) => {
            this._progress.push(this.cretaeScript(item.url, item.version, LAUNCHERVERION));
        });
    }

    private checkLoadStates() {
        const scripts = document.getElementsByName('vf-script');
        const version = LAUNCHERVERION;

        for (let i = 0; i < scripts.length; i++) {
            if (scripts[i].title === version && scripts[i].getAttribute('isLoad') === 'false') {
                return false;
            }
        }

        return true;
    }

    private cretaeScript(src: string, id: string, titile: string) {
        const script = document.createElement('script') as HTMLScriptElement;

        script.setAttribute('name', 'vf-script');
        script.setAttribute('isLoad', 'false');
        script.setAttribute('crossorigin', 'anonymous'); // 页面window.onerror可以拿到引擎中js报错信息
        script.type = 'text/javascript';
        script.id = id;
        script.title = titile;
        script.async = false;
        script.src = src;

        script.onload = this.onJsComplete.bind(this);
        script.onerror = this.onJsError.bind(this);

        document.body.appendChild(script);

        return script;
    }

    private onJsComplete(evt: Event) {
        const script = evt.target as HTMLScriptElement;

        script.setAttribute('isLoad', 'true');

        let isLoadComplete = true;

        this._progress.forEach((value) => {
            if (value.getAttribute('isLoad') === 'false') {
                isLoadComplete = false;
            }
        });

        if (isLoadComplete) {
            this.createEngine();
        }
    }

    private onJsError(evt: any) {
        const script = evt.target as HTMLScriptElement;

        if (this._errorCount > this._errorMax) {
            if (this.errorCall) {
                this.errorCall({
                    code: 'LoadError',
                    level: 'error',
                    data: [script.src],
                });
            }

            return;
        }

        const progress = this._progress;
        const index = progress.indexOf(evt.target as any);
        const cdn = this._engineCDN.default[this._cdnsIndex % this._engineCDN.default.length];

        this._cdnsIndex++;
        this._errorCount++;

        const path =  script.src.substr(script.src.indexOf('/', 10) + 1);

        script.src = cdn + path;

        for (let i = index; i < progress.length; i++) {
            this.removeScriptEvent(progress[i]);
            progress[i].remove();
            progress[i] = this.cretaeScript(progress[i].src, progress[i].id, progress[i].title);
        }
    }

    private removeScriptEvent(script: HTMLScriptElement) {
        script.onload = null;
        script.onerror = null;
    }
    private clearCachePlayer() {
        const w = window as any;

        if (w['vf'] && w['vf']['launcherPlayer'] && w['vf']['launcherPlayer'].dispose) {
            w['vf']['launcherPlayer'].dispose();
            w['vf']['launcherPlayer'] = null;
        }
    }

    /**
     * 创建引擎
     */
    private createEngine() {
        if (this.completeCall) {
            const deviceType = vf.utils.getSystemInfo().device.type;

            if (this._ops.debug && (deviceType === 'mobile' || deviceType === 'tablet')) { // 移动设备显示调试面板
                // eslint-disable-next-line no-new
                const vfConsole = new (window as any).VConsole();

                if (this._ops.showConsole) {
                    vfConsole.show();
                }
            }
            if (this._ops.showFPS) { // 显示帧率
                const stats = new (window as any).Stats();

                stats.showPanel(0); // 0: fps, 1: ms, 2: mb, 3+: custom
                document.body.appendChild(stats.dom);

                const animate = () => {
                    stats.begin();
                    // monitored code goes here
                    stats.end();
                    requestAnimationFrame(animate);
                };

                requestAnimationFrame(animate);
            }
            vf.utils.skipHello();
            if (this._isPlayer) { // 如果排除库中没有player，即创建
                const player = new (window as any)['vf']['player']['Player'](this._ops);

                this.clearCachePlayer();
                (window as any)['vf']['launcherPlayer'] = player;
                this.completeCall(player);
            }
            else {
                this.completeCall(new CreateApp(this._ops));
            }
            vf.utils.versionPrint(this.version);
            this.completeCall = undefined;
            this.errorCall = undefined;
        }
        this._loading.hide();
        this._loading.dispose();
    }
}

export function createVF(options: IVFOptions, completeCall: (player: EngineAPI) => {}, errorCall?: (e: IEvent) => {}) {
    const scripts = document.getElementsByName('vf-script');
    const version = LAUNCHERVERION;

    for (let i = 0; i < scripts.length; i++) {
        if (scripts[i].title !== version && scripts[i].title !== '') {
            deleteVF();
            break;
        }
    }
    // eslint-disable-next-line no-new
    new VIPKIDLauncher(options, completeCall, errorCall);
}
