/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-unused-vars */

import { IVFOptions, EngineAPI } from '@player/IVFEngine';
import { VFStateCode, SceneEvent } from '@player/core/model/IVFData';
import { EventType } from '@player/event/EventType';
import { EventLevel } from '@player/event/EventLevel';

export class CreateApp implements EngineAPI {
    public app: vf.Application;
    public uiStage: vf.gui.Stage;
    public option: IVFOptions;
    private _main?: any;

    /**
     * @private
     * @type {VFStage}
     */
    private _readyState = VFStateCode.INIT;
    public get readyState(): VFStateCode {
        return this._readyState;
    }
    public set readyState(value) {
        this._readyState = value;
        this.uiStage.emit(EventType.STATUS, { code: value, level: EventLevel.STATUS, data: value });
    }

    constructor(options: IVFOptions) {
        // super();
        // 1. 初始化配置
        this.option = options;

        // 2. 初始化引擎 + GUI
        const info = this.createApp();

        this.app = info.app;

        this.uiStage = info.uiStage;

        info.uiStage.on(EventType.MESSAGE, this.sendMessage, this); // 推送消息
        info.uiStage.on(EventType.STATUS, this.onStatus, this); // 状态变化

        this.readyState = VFStateCode.INIT;
    }

    private createApp() {
        const options = this.option;

        const app = this.app = new vf.Application({
            backgroundColor: parseInt(options.bgcolor || '0', 16),
            transparent: options.wmode === 'transparent',
            antialias: true,
            resolution: options.resolution,
            forceCanvas: options.forceCanvas,
            powerPreference: 'low-power',
        });

        options.container.appendChild(this.app.view);
        const vfUtils = vf.gui.Utils;
        const clientRect = vfUtils.getBoundingClientRect(options.container);
        const stageW = vfUtils.formatRelative(options.width, clientRect.width);
        const stageH = vfUtils.formatRelative(options.height, clientRect.height);

        // 创建GUI
        const uiStage = new vf.gui.Stage(stageW, stageH, app);

        // 适配处理, 这块由于播放器项目中的JSON有设置scaleMode属性，所以无法通用，后续可想方式统一创建
        vfUtils.calculateUpdatePlayerSize(options.container, app, uiStage, stageW, stageH, options.scaleMode as any);

        const frameRate = options.frameRate || 30;

        app.ticker.maxFPS = frameRate;
        vf.Ticker.system.maxFPS = frameRate;
        vf.Ticker.shared.stop();
        vf.Ticker.shared.maxFPS = frameRate;
        vf.gui.TickerShared.maxFPS = frameRate;
        vf.gui.Utils.debug = options.debug || false;

        return { app, uiStage };
    }

    public get Main() {
        return this._main;
    }

    /** ===================== ============================ */
    public play(src?: any, packageName?: string) {
        if (src && packageName) {
            vf.utils.readFileSync(src, { moduleName: packageName }).then((ValueClass: any) => {
                this._main = new ValueClass(this);
                this.readyState = VFStateCode.READY;
            });
        }
    }

    // ------------------------- 外部与钩子 ---------------------------//
    /**
     * 接收到服务
     * @param msg
     */
    private sendMessage(msg: vf.IEvent): void {
        if (this.onMessage) {
            this.onMessage(msg);
        }
    }

    /**
     * 接收到的状态
     * @param msg
     */
    private onStatus(msg: vf.IEvent): void {
        if (msg.level === vf.IEventLevel.error || msg.level === vf.IEventLevel.warning) {
            if (this.onError) {
                this.onError(msg);
            }

            return;
        }
        switch (msg.code) {
            case VFStateCode.INIT:
                if (this.onInit) {
                    this.onInit();
                }
                break;
            case VFStateCode.READY:
                if (this.onReady) {
                    this.onReady();
                }
                break;
            case SceneEvent.SceneLoad:
                if (this.onSceneLoad) {
                    this.onSceneLoad(msg);
                }
                break;
            case SceneEvent.LoadProgress:
                if (this.onSceneLoadProgress) {
                    this.onSceneLoadProgress(msg);
                }
                break;
            case SceneEvent.ScenComplete:
                if (this.onSceneCreate) {
                    this.onSceneCreate(msg);
                }
                break;
        }
    }

    private sendCommand(msg: vf.IEvent) {
        if (msg.level === undefined) {
            msg.level = EventLevel.COMMAND as any;
        }
        this.uiStage.emit(EventType.ONMESSAGE, msg);
    }

    /**
     * 设置消息，外部接口
     * @param msg
     */
    public message(msg: vf.IEvent) {
        if (typeof msg !== 'object' || msg.code === undefined || msg.level === undefined) {
            this.onError({ code: 'error', message: 'vf message 消息格式异常', data: msg, level: 'error' });

            return;
        }
        (msg as any).$system = true;
        this.sendCommand(msg);
    }

    public pause() {
        this.sendCommand({ code: 'pause' } as any);
    }
    public resume() {
        this.sendCommand({ code: 'resume' } as any);
    }
    public reset() {
        this.sendCommand({ code: 'reset' } as any);
    }
    public switchToNextScene(transition?: any) {
        this.sendCommand({ code: 'switchToNextScene', data: transition } as any);
    }
    public switchToPrevScene(transition?: any) {
        this.sendCommand({ code: 'switchToPrevScene', data: transition } as any);
    }
    public switchToSceneId(sceneId: string, transition?: any) {
        this.sendCommand({ code: 'switchToSceneId', data: { sceneId, transition } } as any);
    }
    public switchToSceneIndex(index: number, transition?: any) {
        this.sendCommand({ code: 'switchToSceneIndex', data: { index, transition } } as any);
    }

    public dispose(): void {
        if (this.readyState === VFStateCode.DISABLED) {
            return;
        }
        this.readyState = VFStateCode.DISABLED;
        if (this._main && this._main.releaseAll) {
            this._main.releaseAll();
        }
        this._main = undefined;
        this.onDispose();
    }

    public destroy() {
        vf.AudioEngine.Ins().dispose();
        this.dispose();
        this.uiStage.offAll();
        this.uiStage.releaseAll();
        this.app.destroy(true, { children: true, texture: true, baseTexture: true });
        this.option = null as any;
        this.app = null as any;
        this.uiStage = null as any;
    }

    /**
     * 接口，避免写入逻辑
     */
    // eslint-disable-next-line handle-callback-err
    public readonly onError = (err: any) => {
        //
    };

    /**
     * 接口，避免写入逻辑
     */
    public readonly onInit = () => {
        //
    };

    /**
     * 接口，避免写入逻辑
     */
    public readonly onReady = () => {
        //
    };

    /**
     * 接口，避免写入逻辑
     */
    public readonly onSceneLoad = (msg: vf.IEvent) => {
        //
    };
    /**
     * 接口，避免写入逻辑
     */
    public readonly onSceneLoadProgress = (msg: vf.IEvent) => {
        //
    };
    /**
     * 接口，避免写入逻辑
     */
    public readonly onSceneCreate = (msg: vf.IEvent) => {
        //
    };

    /**
     * 接口，避免写入逻辑
     */
    public readonly onMessage = (msg: vf.IEvent) => {
        //
    };

    /**
     * 接口，避免写入逻辑
     */
    public readonly onDispose = () => {
        //
    };
}
